.inCloudconIcon
  position: fixed
  top: 50%
  left: 50%
  transform: translate(-50%, -50%)
  width: 100px
  height: 100px
  color: var(--accent)
  z-index: -20

.isDragAccept
  opacity: .1

.preorderTitle
  color: var(--accent) !important
  border: 1px solid var(--accent)
  padding: 5px
  margin: 0 10px
  border-radius: var(--br)
  font-size: 12px

.OrderLayout
  --window-padding: 0px
  position: relative
  // min-height: 100vh
  outline: none !important
  position: relative
  display: grid
  grid-template-columns: 2fr auto

  .orderActions_box
    background: var(--L0)
    border-radius: var(--br10)
    border: var(--border)

    button:last-of-type:not(:first-of-type)
      border-top-left-radius: 0px
      border-bottom-left-radius: 0px
    button:not(:last-of-type)
      // border-radius: inherit 0 0 0
      border-bottom-right-radius: 0px
      border-top-right-radius: 0px
      border-right: var(--border)

  .page-header
    grid-column-start: 1
    grid-column-end: 3
    border-bottom: var(--border)
    padding: 2px 20px
  .Info
    // background: var(--L0)
    grid-column-start: 3
    overflow-y: auto
    overflow-x: hidden
    height: 100vh
    position: sticky
    top: 0
  .Composition
    border-bottom: var(--border)

.LeftSideContent
  display: grid
  grid-template-columns: 1fr 1fr
  height: fit-content
  width: 100%

  @media ( min-width: 2000px)
    margin: 0 auto
    max-width: 1700px

  .Composition
    grid-column-start: 1
    grid-column-end: -1

  .Docs
    border-left: var(--border)
    min-height: 100px
    grid-column-start: 2
    grid-column-end: -1
